:root {
  --main-color: #3586ff;
}
body {
  margin: 0;
  font-family: 'Noto Naskh Arabic', serif;
font-family: 'Noto Sans Arabic', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#eee;
}
html{
  overflow-x: hidden;
  scroll-padding-top: 9rem;
  scroll-behavior: smooth;
}
html::-webkit-scrollbar{
  width: 1rem;
}
html::-webkit-scrollbar-track{
  background: transparent;
}
html::-webkit-scrollbar-thumb{
  background: var(--main-color);
  border-radius: 5rem;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* Start media query */
@media (min-width:768px) {
  .container {
    width: 750px;
  }
}
@media (min-width:991px) {
  .container {
    width: 950px;
  }
}
@media (min-width:1200px) {
  .container {
    width: 1150px;
  }
}
.container {
  width: 90% !important ;
  margin: 0 ;
  padding: 0 15px !important;
}
/* End Media Query */
/* Start navBar  */
.nav {
  width: 100%;
  position: fixed;
  top: 0 ;
  left: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.nav .container {
  display: flex;
  justify-content: space-between;
  align-items:center;
}
.nav .container .logo {
  width: 10%;
}
.nav .container .logo img {
  width: 110px;
  border-radius: 50%;
}
.nav .container .links {
  direction: rtl;
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav .container .links button {
  font-size: 26px;
  font-weight: bold;
  background-color: transparent;
  border: none;
  color:var(--main-color);
  cursor: pointer;
  position:relative;
}
.nav .container .links button::after {
  content:" ";
  position:absolute;
  bottom: 0;
  left: 0 ;
  border-radius: 10px;
  background:transparent;
  width: 100%;
  height: 2px;
  transition:.5s;
}
.nav .container .links button:hover::after {
  bottom: -20px;
  background:var(--main-color);
}
.showNav {
  display: block;
  background:white;
  box-shadow: 2px 3px 4px 0px rgb(0 0 0 / 15%);
  width: 100%;
  position: fixed;
  top: 0 ;
  left: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.menu {
  display: none;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.nav .top {
  position: fixed;
  bottom: 2000px;
  left: 20px;
  cursor:pointer;
  padding: 2px 10px;
  font-size: 30px;
  color:white;
  background:var(--main-color);
  border-radius: 19px;
  transition: .5s;
}
.showtop {
  bottom: 20px !important;
  transition: .5s;
}
/* Start Media Query for Mobile to NavBar */
@media (max-width: 800px) {
  .nav .container .links {
    position: absolute;
    left: -2000px;
    top: 115px;
    width: 70%;
    background: var(--main-color);
    color: white;
    flex-direction: column;
    justify-content: start;
    height: 104vh;
    z-index: -1;
    transition: .6s;
  }
  .showLinks {
    left: -20px !important;
  }
  .nav .container .links button {
    color:white;
    margin-top: 120px;
  }
  .menu {
    display: block;
  }
  .menu i {
    font-size: 30px;
    color:var(--main-color);
    cursor: pointer;
  }
}










/* Start Home Styling */
.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
  .home .background {
  width: 100%;
  position: relative;
  top: 0;
  height:100vh;
  z-index: -1;
  }
  .home .background img {
    height: 100%;
    width: 100%;
  }
  .home .container .home_text {
    position:absolute;
    top: 110px;
    right: 30px;
    direction: rtl;
  }
  .home .container .home_text h1 {
    font-size: 30px;
    color:white;
  }
  .home .container .home_text h2  {
    font-size: 20px;
    color:white;
    margin: 0;
  }
  .home .container .home_text h3  {
    font-size: 20px;
    color:white;
    margin: 0;
  }
.call {
  position: fixed;
  bottom: 20px;
  right: 20px;
}
.call i {
  font-size: 30px;
  padding: 15px;
  background:var(--main-color);
  color:white;
  border-radius: 30px;
}
@media (max-width:800px) {
  .home .container .home_text {
    right: 0;
    margin-top: 50px;
    text-align:center;
    width:100%;
  }
  .home .background {
    padding-top: 0;
    }
  
}
/* End Home Styling */
/* Start Why Styling */
.why {
  width:100%;
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.why .container {
  text-align: center;
  padding-top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.why .container p {
  font-size: 18px;
  line-height: 2;
}
.why .text {
  width:100%;
  list-style-position: inside;
  direction: rtl;
  text-align: right;
}
.why .text ul li{
  font-size: 20px;
  line-height: 2;
}
.why .container .sec_two , .why .container .sec_three  {
  display: flex;
  justify-content: space-between;
  align-items:start;
  margin: 30px 0 ;
}
.why .container .sec_two .img , .why .container .sec_three .img {
  width: 50%;
}
.why .container .sec_two .img img , .why .container .sec_three .img img{
  max-width: 100%;
  border-radius: 20px;
  height: 450px;
}
.why .container .sec_two .text , .why .container .sec_three .text {
  width: 50%;
}
.why .container .sec_two .text {
  margin-left: 20px;
}
.why .container .sec_three .text {
  margin-right: 20px;
}
.why .container .sec_three p, .why .container .sec_two p  {
  font-size: 19px;
  line-height: 2
}
.why .gallery  {
  position:relative;
  width: 70%;
  height: 80vh;
  margin: 40px 0 90px;
}
.why .gallery img {
  z-index: -1;
  border-radius: 20px;
  width: 100%;
  height: 100%;
}
.why .gallery .toleft  {
  position:absolute;
  top: 50%;
  left: 15px;
  cursor: pointer;
}
.why .gallery .toright  {
  position:absolute;
  top: 50%;
  right: 15px;
  cursor: pointer;
}
.why .gallery  i {
  font-size: 50px;
  color:white;
}
@media (max-width: 800px) {
  .why .sec_two , .why .sec_three {
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .why .container .sec_two .text, .why .container .sec_three .text  {
    width: 100%;
  }
  .why .container .sec_two .img, .why .container .sec_three .img {
    width: 100%;
  }
  .why .gallery {
    width: 100%;
    height: 60vh;
  }
}
/* End Why Styling */
/* Start Who Section */
.who {
  padding-top: 200px;
  padding-bottom: 100px;
  width: 100%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
}
.who .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.who .container .secOne {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items:center;
}
.who .container .secOne img{
  width:100%;
  border-radius: 20px;
  margin-right: 20px;
}
.who .container .secOne p{
 direction:rtl;
 font-size: 20px;
 line-height: 2;
}
.who .version {
  font-size: 20px;
  line-height: 2;
  direction:rtl;
}
@media (max-width:800px) {
  .who .container .secOne {
    flex-direction: column;
  }
  .who .container .secOne img {
    width: 100%;
  }
  .who .container .secOne p {
    width: 100%;
  }
}
/* End Who Section */
/* Start Services Styling */
.services {
  width: 100%;
  display:flex;
  justify-content:center;
  align-items:center;
  flex-direction:column;
  padding-bottom: 50px;
}
.services .back {
  width: 100%;
  height: 80vh;
}
.services .back img{
  width: 100%;
  height: 100%;
}
.services .container {
  display:flex;
  padding: 200px 0 ;
  justify-content: center;
  align-items:center;
  flex-direction:column;
  padding: 50px 0;
}
.services .container p  {
 direction: rtl;
 font-size: 20px;
 line-height: 2;
}
.services .container .servs {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px,1fr));
  gap: 20px;
  margin-top: 50px;
  direction: rtl;
}
.services .container .servs div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 500px;
}
.services .container .servs div img {
  width: 350px;
  height: 300px;
  border-radius: 20px;
}
.services .container .servs div h4 {
  font-size: 22px;
  margin: 10px;
  color:var(--main-color)
}
.services .container .servs div p {
  font-size: 19px;
  margin: 10px;
}
/* End Services Styling */
/* Start Contact us page  */
.contact {
  padding: 122px 0;
  display:flex;
  justify-content: center;
  align-items:center;
  width: 100%;
  flex-direction: column;
  direction: rtl;
}
.contact .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact img {
  width: 100%;
  height: 60vh;
}
.contact p {
  font-size: 21px;
  font-weight: 400;
  line-height: 2;
}
.contact .container .ways {
  display:grid;
  grid-template-columns: repeat(auto-fill,minmax(300px, 1fr));
  gap: 30px;
  margin-top: 100px;
  width:100%;
}
.contact .container .ways a {
  background:white;
  padding: 30px;
  display: flex;
  justify-content: space-between;
  align-items:center;
  flex-direction: column;
  border-radius: 20px;
  height:300px;
  transition: .4s;
}
.contact .container .ways a:hover {
  background:var(--main-color);
  color:white;
}
.contact .container .ways a h3 {
  font-size: 28px;
}
.contact .container .ways a i {
  font-size: 45px;
  color:var(--main-color);
  transition:.4s;
}
.contact .container .ways a:hover i {
  color:white;
}
.contact .container .ways a h4 {
  font-size: 21px;
}
.contact .container .maps {
  margin-top: 150px;
  width: 100%;
}
/* End Contact us page  */
/* Start Footer Styling */
.footer {
  position: relative;
  width: 100%;
  background: #3586ff;
  min-height: 100px;
  display: flex;
  padding: 20px 0 ;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
  z-index: -1;
}
.footer .container {
  display:flex;
  flex-direction: column;
  justify-content:center;
  align-content: center;
}
.social-icon,
.footer-menu {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  flex-wrap: wrap;
}
.social-icon__item,
.menu__item {
  list-style: none;
}
.menu__item {
  margin: 0 20px;
}
.social-icon__link {
  font-size: 2rem;
  color: #fff;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s;
}
.social-icon__link:hover {
  transform: translateY(-10px);
}

.menu__link {
  font-size: 1.2rem;
  color: #fff;
  margin: 0 10px;
  display: inline-block;
  transition: 0.5s;
  text-decoration: none;
  opacity: 0.75;
  font-weight: 300;
}

.menu__link:hover {
  opacity: 1;
}

.footer p {
  color: #fff;
  margin: 15px 0 10px 0;
  font-size: 1rem;
  font-weight: 300;
}

.wave {
  position: absolute;
  top: -100px;
  left: 0;
  width: 100%;
  height: 100px;
  background: url("https://i.ibb.co/wQZVxxk/wave.png");
  background-size: 1000px 100px;
}

.wave#wave1 {
  z-index: 1000;
  opacity: 1;
  bottom: 0;
  animation: animateWaves 4s linear infinite;
}

.wave#wave2 {
  z-index: 999;
  opacity: 0.5;
  bottom: 10px;
  animation: animate 4s linear infinite !important;
}

.wave#wave3 {
  z-index: 1000;
  opacity: 0.2;
  bottom: 15px;
  animation: animateWaves 3s linear infinite;
}

.wave#wave4 {
  z-index: 999;
  opacity: 0.7;
  bottom: 20px;
  animation: animate 3s linear infinite;
}

@keyframes animateWaves {
  0% {
    background-position-x: 1000px;
  }
  100% {
    background-positon-x: 0px;
  }
}

@keyframes animate {
  0% {
    background-position-x: -1000px;
  }
  100% {
    background-positon-x: 0px;
  }
}
@media(max-width:800px) {
  .footer-menu {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0; 
    margin:30px 0 ;

  }
  .footer-menu .menu__item {
    font-size: 15px;
    margin: 0;
  }
  .social-icon {
    padding: 0;
  }
}
/* End Footer Styling */







/* Global Styling */
.main-title  {
  color:white;
  margin: 20px 0 ;
  background-color: var(--main-color);
  padding: 10px 20px;
  border-radius: 15px;
  font-size: 30px;
  width:fit-content;  
}
a {
  text-decoration: none;
  color:black;
}
@media(max-width:800px) {
  .main-title {
    font-size:  25px;
    padding: 10px;
    text-align: center;
    width: 90%;
  }
}
.hideShow {
  display:none;
}